import React,{useState} from 'react'
import {graphql, Link, useStaticQuery} from 'gatsby'
import Img from "gatsby-image"
import LightBox from "../../../../Global/App/Lightbox/Lightbox.component"
import styled from 'styled-components'
const ImgContainer = styled.div`
    cursor:pointer;
`
function BannerSingle2(){
	
  const data = useStaticQuery(graphql`
		query{
			all: allFile(filter: { absolutePath: { regex:"/singlev2/" } }) {
            edges {
              node {
                childImageSharp {
                  fluid(maxHeight: 500) {
                    ...GatsbyImageSharpFluid
                    presentationWidth
                  }
                }
              }
            }
          }
        }`
	)
	const images = data.all.edges
	const [showLightbox, setShowLightbox] = useState(false)
  	const [selectedImage, setSelectedImage] = useState(null)

	const handleOpen = i => e => {
		setShowLightbox(true)
		setSelectedImage(i)
	}
	const handleClose = () => {
		setShowLightbox(false)
		setSelectedImage(null)
	}
	const handlePrevRequest = (i, length) => e => {
		setSelectedImage((i - 1 + length) % length)
	}
	const handleNextRequest = (i, length) => e => {
		setSelectedImage((i + 1) % length)
	}
	const isSSR = typeof window === "undefined";
    return (
		<div>
		<div className="single_page_listing_style">
			<div className="container-fluid p0">
				<div className="row">
					{!isSSR && showLightbox && selectedImage !== null && (
						<LightBox
							images={images}
							handleClose={handleClose}
							handleNextRequest={handleNextRequest}
							handlePrevRequest={handlePrevRequest}
							selectedImage={selectedImage}
						/>
						)}
					<div className="col-sm-6 col-lg-6 p0">
						<div className="row m0">
							<div className="col-lg-12 p0">
							{!isSSR && (	<ImgContainer className="spls_style_one pr1 1px" onClick={handleOpen(3)} >
								<Img
									fluid={images[3].node.childImageSharp.fluid}  
									className="img-fluid w100"      
									/>
								</ImgContainer>)}
							</div>
						</div>
					</div>
					<div className="col-sm-6 col-lg-6 p0">
							{!isSSR && (
								<div className="row m0">
							<div className="col-sm-6 col-lg-6 p0">
								<ImgContainer className="spls_style_one" onClick={handleOpen(0)} >
									<Img
									fluid={images[0].node.childImageSharp.fluid}  
									className="img-fluid w100"      
									/>
								</ImgContainer>
							</div>
							<div className="col-sm-6 col-lg-6 p0">
								<ImgContainer className="spls_style_one" onClick={handleOpen(1)} >
									<Img
									fluid={images[1].node.childImageSharp.fluid}  
									className="img-fluid w100"      
									/>
								</ImgContainer>
							</div>
							<div className="col-sm-6 col-lg-6 p0">
								<ImgContainer className="spls_style_one" onClick={handleOpen(4)} >
									<Img
									fluid={images[4].node.childImageSharp.fluid}  
									className="img-fluid w100"      
									/>
								</ImgContainer>
							</div>
							<div className="col-sm-6 col-lg-6 p0">
								<ImgContainer className="spls_style_one" onClick={handleOpen(2)} >
									<Img
									fluid={images[2].node.childImageSharp.fluid}  
									className="img-fluid w100"      
									/>
								</ImgContainer>
							</div>
						</div>
					
							)}
					</div>
				</div>
			</div>
		</div>
		<div className="p0">
			<div className="container">
				<div className="row listing_single_row">
					<div className="col-sm-6 col-lg-7 col-xl-8">
						<div className="single_property_title">
							<Link to="#" className="upload_btn popup-img" onClick={handleOpen(3)}><span className="flaticon-photo-camera"></span> View Photos</Link>
						</div>
					</div>
					<div className="col-sm-6 col-lg-5 col-xl-4">
						<div className="single_property_social_share">
							<div className="spss style2 mt10 text-right tal-400">
								<ul className="mb0">
									<li className="list-inline-item"><Link to="#"><span className="flaticon-transfer-1"></span></Link></li>
									<li className="list-inline-item"><Link to="#"><span className="flaticon-heart"></span></Link></li>
									<li className="list-inline-item"><Link to="#"><span className="flaticon-share"></span></Link></li>
									<li className="list-inline-item"><Link to="#"><span className="flaticon-printer"></span></Link></li>
								</ul>
							</div>
						</div>
					</div>
				</div>
			</div>
		</div>
	</div>
)
					
}

export default BannerSingle2